import request from './request'

/**
 * Ëé∑ÂèñÊóÖË°åÂàóË°®
 * @param {Object} params - Êü•ËØ¢ÂèÇÊï∞
 */
export const getTripList = (params) => {
    return request.get('/trips/', { params })
}

/**
 * Ëé∑ÂèñÊóÖË°åËØ¶ÊÉÖ
 * @param {string} slug - ÊóÖË°åÊ†áËØÜÁ¨¶
 * ÂÖàÂ∞ùËØï‰ªéÊñ∞TripÊ®°ÂûãËé∑ÂèñÔºåÂ§±Ë¥•ÂàôÂ∞ùËØïÊóßSiteStat
 */
export const getTripDetail = async (slug) => {
    // ÂÖàÂ∞ùËØïÊñ∞ÁöÑTripÊ®°Âûã
    try {
        return await request.get(`/trip-plans/${slug}/`)
    } catch (error) {
        // 404ÊàñÁΩëÁªúÈîôËØØÔºåÂ∞ùËØïÊóßÁöÑSiteStat
        const statusCode = error.response?.status || error.status
        const shouldFallback = statusCode === 404 ||
            error.code === 'ERR_BAD_REQUEST' ||
            error.message?.includes('404') ||
            !error.response

        if (shouldFallback) {
            return await request.get(`/trips/${slug}/`)
        }
        throw error
    }
}

