<template>
  <div class="trip-detail-container" :style="{ background: (trip && trip.theme_color) || '#f0e68c' }">
    <!-- ÂØºËà™Ê†è -->
    <NavBar />
    
    <!-- ËøîÂõûÊåâÈíÆ -->
    <button class="back-btn" @click="goBack" title="ËøîÂõûÈ¶ñÈ°µ">
      üè†
    </button>
    
    <!-- Ë∑≥ËΩ¨Âà∞ËØÑËÆ∫ÊåâÈíÆÔºàÂè≥‰∏äËßíÔºâ -->
    <button class="scroll-btn" @click="scrollToComments" title="Ë∑≥Âà∞ËØÑËÆ∫Âå∫">
      ‚¨áÔ∏è
    </button>
    
    <!-- ËÉåÊôØÈü≥‰πêÊåâÈíÆ -->
    <button class="music-btn" @click="toggleMusic" :title="isPlaying ? 'ÊöÇÂÅúÈü≥‰πê' : 'Êí≠ÊîæÈü≥‰πê'">
      {{ isPlaying ? 'üîä' : 'üîá' }}
    </button>
    
    <audio ref="audioPlayer" :src="musicSrc || ''" preload="auto" loop></audio>
    
    <div class="container py-5">
      <!-- LoadingÁä∂ÊÄÅ -->
      <div v-if="loading" class="text-center">
        <div class="spinner-border text-primary" role="status">
          <span class="visually-hidden">Âä†ËΩΩ‰∏≠...</span>
        </div>
      </div>
      
      <!-- ÊóÖË°åËØ¶ÊÉÖ -->
      <div v-else-if="trip">
        <!-- È°µÈù¢Ê†áÈ¢ò -->
        <div class="card shadow-lg mb-4">
          <div class="card-body p-5">
            <h1 class="mb-3">{{ trip.name || trip.title }}</h1>
            <p class="text-muted mb-0">{{ trip.description }}</p>
          </div>
        </div>
        
        <!-- ÊóÖË°åËøõÂ∫¶Êù°ÁªÑ‰ª∂ ‚≠ê -->
        <TripProgress
          v-if="(tripConfig && tripConfig.dates) || (trip.start_date && trip.end_date)"
          :start-date="tripConfig?.dates?.start || trip.start_date"
          :end-date="tripConfig?.dates?.end || trip.end_date"
        />
        
        <!-- Ë°åÁ®ãÊ¶ÇËßàÁªÑ‰ª∂ ‚≠ê (Âä®ÊÄÅÂÜÖÂÆπ) -->
        <TripOverview v-if="tripConfig && tripConfig.overview" title="Ë°åÁ®ãÊ¶ÇËßà">
          <!-- Âü∫Êú¨‰ø°ÊÅØ -->
          <div v-if="tripConfig.overview.basicInfo" class="basic-info-section">
            <h4>üß≠ Âü∫Êú¨‰ø°ÊÅØ</h4>
            <div class="info-grid">
              <div v-if="tripConfig.overview.basicInfo.participants" class="info-item">
                <span class="info-label">üë• RoamioerÔºö</span>
                <span class="info-value">{{ tripConfig.overview.basicInfo.participants }}</span>
              </div>
              <div v-if="tripConfig.overview.basicInfo.departure" class="info-item">
                <span class="info-label">üö© Âá∫ÂèëÂú∞Ôºö</span>
                <span class="info-value">{{ tripConfig.overview.basicInfo.departure }}</span>
              </div>
              <div v-if="tripConfig.overview.basicInfo.destination" class="info-item">
                <span class="info-label">üéØ ÁõÆÁöÑÂú∞Ôºö</span>
                <span class="info-value">{{ tripConfig.overview.basicInfo.destination }}</span>
              </div>
              <div v-if="tripConfig.overview.basicInfo.transport" class="info-item">
                <span class="info-label">üöó ‰∫§ÈÄöÊñπÂºèÔºö</span>
                <span class="info-value">{{ tripConfig.overview.basicInfo.transport }}</span>
              </div>
              <div v-if="tripConfig.overview.basicInfo.accommodation" class="info-item">
                <span class="info-label">üè® ‰ΩèÂÆøÔºö</span>
                <span class="info-value">{{ tripConfig.overview.basicInfo.accommodation }}</span>
              </div>
              <div v-if="tripConfig.overview.basicInfo.duration" class="info-item">
                <span class="info-label">‚è±Ô∏è Ë°åÁ®ãÊó∂ÈïøÔºö</span>
                <span class="info-value">{{ tripConfig.overview.basicInfo.duration }}</span>
              </div>
              <div v-if="tripConfig.overview.basicInfo.budget" class="info-item">
                <span class="info-label">üí∞ È¢ÑÁÆóÔºö</span>
                <span class="info-value">{{ tripConfig.overview.basicInfo.budget }}</span>
              </div>
              <div v-if="tripConfig.overview.basicInfo.theme" class="info-item">
                <span class="info-label">üé® ‰∏ªÈ¢òÔºö</span>
                <span class="info-value">{{ tripConfig.overview.basicInfo.theme }}</span>
              </div>
            </div>
          </div>

          <!-- Ë°åÁ®ã‰∫ÆÁÇπ -->
          <h4>üìç Ë°åÁ®ã‰∫ÆÁÇπ</h4>
          <ul class="highlights-list">
            <li v-for="(highlight, index) in tripConfig.overview.highlights" :key="index">
              {{ highlight }}
            </li>
          </ul>
          
          <!-- Ë°åÁ®ãÂÆâÊéí -->
          <h4>üóìÔ∏è ËØ¶ÁªÜË°åÁ®ã</h4>
          <div class="itinerary-section">
            <div v-for="(item, index) in tripConfig.overview.itinerary" :key="index" class="itinerary-item">
              <div class="itinerary-header">
                <strong class="itinerary-day">{{ item.day }}</strong>
                <span v-if="item.time" class="itinerary-time">‚è∞ {{ item.time }}</span>
              </div>
              <div class="itinerary-content">{{ item.content }}</div>
              <div v-if="item.highlight" class="itinerary-highlight">
                {{ item.highlight }}
              </div>
            </div>
          </div>
          
          <!-- È¢ÑÁÆóÂèÇËÄÉ -->
          <div v-if="tripConfig.overview.budget && tripConfig.overview.budget.items">
            <h4>üí∞ È¢ÑÁÆóÂèÇËÄÉ</h4>
            <table>
              <thead>
                <tr>
                  <th>È°πÁõÆ</th>
                  <th>ÈáëÈ¢ùÔºàÂÖÉÔºâ</th>
                  <th>Â§áÊ≥®</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in tripConfig.overview.budget.items" :key="index">
                  <td>{{ item.name }}</td>
                  <td>¬•{{ item.amount }}</td>
                  <td>{{ item.note }}</td>
                </tr>
                <tr class="total-row">
                  <td><strong>ÊÄªËÆ°</strong></td>
                  <td><strong>¬•{{ tripConfig.overview.budget.total }}</strong></td>
                  <td>‰∫∫ÂùáÈ¢ÑÁÆó</td>
                </tr>
              </tbody>
            </table>
          </div>

          <!-- ÂÆûÁî®ÊèêÁ§∫ -->
          <div v-if="tripConfig.overview.tips && tripConfig.overview.tips.length" class="tips-section">
            <h4>üí° ÂÆûÁî®ÊèêÁ§∫</h4>
            <ul class="tips-list">
              <li v-for="(tip, index) in tripConfig.overview.tips" :key="index">
                {{ tip }}
              </li>
            </ul>
          </div>
        </TripOverview>
        
        <!-- Â¶ÇÊûúÊ≤°ÊúâÈÖçÁΩÆÔºåÊòæÁ§∫ÈªòËÆ§ÂÜÖÂÆπ -->
        <TripOverview v-else title="Ë°åÁ®ãÊ¶ÇËßà">
          <p class="text-muted text-center">Ë°åÁ®ãÂÜÖÂÆπÊ≠£Âú®Á≠πÂàí‰∏≠ÔºåÊï¨ËØ∑ÊúüÂæÖ...</p>
        </TripOverview>
        
        <!-- ÁªüËÆ°ÁªÑ‰ª∂ÔºöÊòæÁ§∫ÊµèËßàÈáè‰∏éÁÇπËµûÔºõËã•Êó†ÁªüËÆ°ÂàôÊòæÁ§∫0Âπ∂Á¶ÅÁî®ÁÇπËµûÔºàÁßªÂä®Ëá≥ËØÑËÆ∫Âå∫‰∏äÊñπÔºâ -->
        <TripStats
          :views="trip.stats?.views || 0"
          :likes="trip.stats?.likes || 0"
          :can-like="trip.overview ? (trip.visibility === 'public') : true"
          @like="handleLike"
        />
        
        <!-- ËØÑËÆ∫Âå∫ÁªÑ‰ª∂ ‚≠ê -->
        <CommentSection
          ref="commentSectionRef"
          :comments="comments"
          :is-admin="isAdmin"
          :is-author="isAuthor"
          :get-avatar-url="getAvatarUrl"
          @submit-comment="handleSubmitComment"
          @delete-comment="handleDeleteComment"
          @add-image="handleAddImage"
          @update-comment="handleUpdateComment"
          @submit-reply="handleSubmitReply"
          @load-replies="handleLoadReplies"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted, computed, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useUserStore } from '@/stores'
import { getTripDetail, likeTrip, getTripStats, getTripPlanStats, likeTripPlan, viewTripPlan } from '@/api/trip'
import { getCommentList, createComment, deleteComment, addCommentImage, updateComment, getCommentReplies } from '@/api/comment'
import { getAvatarUrl } from '@/config/api'
import { getTripConfig } from '@/config/tripConfig'
import NavBar from '@/components/NavBar.vue'
import TripProgress from '@/components/TripProgress.vue'
import TripStats from '@/components/TripStats.vue'
import TripOverview from '@/components/TripOverview.vue'
import CommentSection from '@/components/CommentSection.vue'

export default {
  name: 'TripDetailView',
  
  components: {
    NavBar,
    TripProgress,
    TripStats,
    TripOverview,
    CommentSection
  },
  
  setup() {
    const route = useRoute()
    const router = useRouter()
    const userStore = useUserStore()
    
    const trip = ref(null)
    const comments = ref([])
    const loading = ref(true)
    const tripConfig = ref(null)
    
    // Èü≥‰πêÁõ∏ÂÖ≥
    const isPlaying = ref(false)
    const audioPlayer = ref(null)
    const musicSrc = computed(() => {
      // ÊòæÂºèÈÄâÊã©‚ÄúÊó†ËÉåÊôØÈü≥‰πê‚ÄùÊó∂ËøîÂõûÁ©∫Â≠óÁ¨¶‰∏≤ÔºõÊú™ËÆæÁΩÆÊó∂‰ΩøÁî®ÈªòËÆ§Èõ®Â£∞
      const bg = trip.value?.background_music
      if (bg === '') return ''
      return bg || '/static/music/rain.mp3'
    })
    
    const isAdmin = computed(() => userStore.isAdmin)
    // ÁôªÂΩïÊ†°È™åÔºöÊú™ÁôªÂΩïÂàôË∑≥ËΩ¨ÁôªÂΩïÈ°µÂπ∂Â∏¶ next
    const ensureLoggedIn = () => {
      if (!userStore.isLoggedIn) {
        const next = route.fullPath || window.location.pathname
        router.push({ path: '/login/', query: { next } })
        return false
      }
      return true
    }
    
    // Âà§Êñ≠ÊòØÂê¶‰∏∫ÊóÖË°å‰ΩúËÄÖ
    const isAuthor = computed(() => {
      return trip.value?.author?.id === userStore.userInfo?.id
    })
    
    // Ëé∑ÂèñÊóÖË°åËØ¶ÊÉÖ
    const fetchTripDetail = async () => {
      const slug = route.params.slug
      try {
        trip.value = await getTripDetail(slug)
        // Á°Æ‰øùÊúâstatsÂØπË±°Áî®‰∫éÂâçÁ´ØÂ±ïÁ§∫
        if (!trip.value.stats) {
          trip.value.stats = { views: 0, likes: 0 }
        }
        
        // Â¶ÇÊûúÊòØÊñ∞TripÊ®°ÂûãÔºå‰∏çÈúÄË¶ÅgetTripConfig
        // Â¶ÇÊûúÊòØÊóßSiteStatÊ®°ÂûãÔºå‰ΩøÁî®getTripConfig
        if (trip.value.overview) {
          // Êñ∞TripÊ®°ÂûãÔºåÂ∑≤ÁªèÊúâÂÆåÊï¥ÁöÑÈÖçÁΩÆÂíåÊï∞ÊçÆ
          // Â§ÑÁêÜ budget.summary -> budget.total ÁöÑÂÖºÂÆπÊÄß
          const overview = { ...trip.value.overview }
          if (overview.budget && overview.budget.summary) {
            overview.budget.total = overview.budget.summary
          }
          
          tripConfig.value = {
            dates: {
              start: trip.value.start_date,
              end: trip.value.end_date
            },
            overview
          }
        } else {
          // ÊóßSiteStatÊ®°ÂûãÔºå‰ΩøÁî®ÈùôÊÄÅÈÖçÁΩÆ
          tripConfig.value = getTripConfig(slug)
        }
        // È¶ñÊ¨°Ëé∑ÂèñÂÆûÊó∂ÁªüËÆ°
        try {
          let stats
          if (trip.value && trip.value.overview) {
            stats = await getTripPlanStats(slug)
          } else {
            stats = await getTripStats(slug)
          }
          if (stats) {
            trip.value.stats = { views: stats.views, likes: stats.likes }
          }
        } catch (e) { /* ignore */ }
        
        // Êõ¥Êñ∞È°µÈù¢Ê†áÈ¢ò‰∏∫ÊóÖË°åÂêçÁß∞ÔºàÂéªÊéâÂπ≥Âè∞ÂêéÁºÄÔºâ
        if (trip.value?.name || trip.value?.title) {
          document.title = `${trip.value.name || trip.value.title}`
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÊóÖË°åËØ¶ÊÉÖÂ§±Ë¥•:', error)
        if (error.response?.status === 404 || error.status === 404) {
          alert('ÊóÖË°åÈ°µÈù¢‰∏çÂ≠òÂú®')
          router.push('/')
        }
      }
    }
    
    // Ëé∑ÂèñËØÑËÆ∫ÂàóË°®
    const fetchComments = async () => {
      const slug = route.params.slug
      try {
        const data = await getCommentList({ trip: slug })
        comments.value = data.results || data || []
      } catch (error) {
        console.error('Ëé∑ÂèñËØÑËÆ∫ÂàóË°®Â§±Ë¥•:', error)
        comments.value = []
      } finally {
        loading.value = false
      }
    }
    
    // ÁÇπËµû
    const handleLike = async () => {
      if (!ensureLoggedIn()) return
      try {
        const slug = route.params.slug
        let result
        if (trip.value && trip.value.overview) {
          result = await likeTripPlan(slug)
        } else {
          result = await likeTrip(slug)
        }
        if (trip.value.stats) {
        trip.value.stats.likes = result.likes
        }
        alert('ÁÇπËµûÊàêÂäüÔºÅ')
      } catch (error) {
        console.error('ÁÇπËµûÂ§±Ë¥•:', error)
        alert('ÁÇπËµûÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï')
      }
    }
    
    // Êèê‰∫§ËØÑËÆ∫ÔºàÊé•Êî∂ÁªÑ‰ª∂‰º†Êù•ÁöÑÊï∞ÊçÆÔºâ
    const handleSubmitComment = async (commentData) => {
      if (!ensureLoggedIn()) return
      try {
        const formData = new FormData()
        formData.append('page', route.params.slug)
        
        if (commentData.content) {
          formData.append('content', commentData.content)
        }
        if (commentData.image) {
          formData.append('image', commentData.image)
        }
        if (commentData.video) {
          formData.append('video', commentData.video)
        }
        
        await createComment(formData)
        alert('ËØÑËÆ∫ÂèëË°®ÊàêÂäüÔºÅ')
        await fetchComments()
      } catch (error) {
        console.error('ÂèëË°®ËØÑËÆ∫Â§±Ë¥•:', error)
        alert('ÂèëË°®ËØÑËÆ∫Â§±Ë¥•Ôºö' + error.message)
      }
    }
    
    // Âà†Èô§ËØÑËÆ∫
    const handleDeleteComment = async (commentId) => {
      // Âà†Èô§ÂâçÊèêÁ§∫Á°ÆËÆ§
      if (!confirm('Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÊù°ÂÜÖÂÆπÂêóÔºüÂà†Èô§ÂêéÊó†Ê≥ïÊÅ¢Â§ç„ÄÇ')) {
        return
      }
      
      // ÂÖàÂêëÂêéÁ´ØÂèëÈÄÅÂà†Èô§ËØ∑Ê±Ç
      try {
        await deleteComment(commentId)
      } catch (error) {
        console.error('Âà†Èô§ËØÑËÆ∫Â§±Ë¥•:', error)
        const errorMsg = error.response?.data?.detail || error.message || 'Âà†Èô§Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
        alert(errorMsg)
        return
      }
      
      // Âà†Èô§ÊàêÂäüÂêéÔºå‰ªéUI‰∏≠ÁßªÈô§
      const deletedComment = comments.value.find(c => c.id === commentId)
      
      if (!deletedComment) {
        // Â¶ÇÊûúÊòØÂõûÂ§çÔºå‰ªéreplyLists‰∏≠Âà†Èô§
        for (const comment of comments.value) {
          if (commentSectionRef.value && commentSectionRef.value.replyLists) {
            const replyList = commentSectionRef.value.replyLists[comment.id]
            if (replyList && replyList.some(r => r.id === commentId)) {
              const filteredReplies = replyList.filter(r => r.id !== commentId)
              if (commentSectionRef.value.updateReplyList) {
                commentSectionRef.value.updateReplyList(comment.id, filteredReplies)
              }
              return
            }
          }
        }
        return
      }
      
      const isReply = !!deletedComment.parent_id
      const parentId = deletedComment.parent_id
      let parentComment = null
      
      if (isReply) {
        // ËøôÊòØÂõûÂ§çÁöÑÂà†Èô§
        parentComment = comments.value.find(c => c.id === parentId)
        if (parentComment && parentComment.replies) {
          // Á´ãÂç≥‰ªéUI‰∏≠ÁßªÈô§
          parentComment.replies = parentComment.replies.filter(r => r.id !== commentId)
          // Êõ¥Êñ∞CommentSectionÁöÑreplyLists
          if (commentSectionRef.value && commentSectionRef.value.updateReplyList) {
            commentSectionRef.value.updateReplyList(parentId, parentComment.replies)
          }
        }
      } else {
        // ËøôÊòØÈ°∂Â±ÇËØÑËÆ∫ÁöÑÂà†Èô§Ôºå‰ªéÂàóË°®‰∏≠Á´ãÂç≥ÁßªÈô§
        comments.value = comments.value.filter(c => c.id !== commentId)
      }
    }
    
    // Ê∑ªÂä†ÂõæÁâá
    const handleAddImage = async ({ commentId, file }) => {
      try {
        const formData = new FormData()
        formData.append('image', file)
        
        await addCommentImage(commentId, formData)
        alert('ÂõæÁâáÊ∑ªÂä†ÊàêÂäüÔºÅ')
        await fetchComments()
      } catch (error) {
        console.error('Ê∑ªÂä†ÂõæÁâáÂ§±Ë¥•:', error)
        alert('Ê∑ªÂä†ÂõæÁâáÂ§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
      }
    }
    
    // Êõ¥Êñ∞ËØÑËÆ∫
    const handleUpdateComment = async ({ commentId, content }) => {
      try {
        await updateComment(commentId, { content })
        alert('ËØÑËÆ∫Êõ¥Êñ∞ÊàêÂäüÔºÅ')
        await fetchComments()
      } catch (error) {
        console.error('Êõ¥Êñ∞ËØÑËÆ∫Â§±Ë¥•:', error)
        alert('Êõ¥Êñ∞ËØÑËÆ∫Â§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
      }
    }
    
    // Êèê‰∫§ÂõûÂ§ç
    const handleSubmitReply = async ({ commentId, content }) => {
      if (!ensureLoggedIn()) return
      try {
        const formData = new FormData()
        formData.append('content', content)
        formData.append('page', route.params.slug)
        formData.append('parent', commentId)  // ËÆæÁΩÆÁà∂ËØÑËÆ∫ID
        
        await createComment(formData)
        alert('ÂõûÂ§çÊàêÂäüÔºÅ')
        // Êèê‰∫§ÊàêÂäüÂêéÈáçÊñ∞Âä†ËΩΩËØ•ËØÑËÆ∫ÁöÑÂõûÂ§çÂàóË°®
        await handleLoadReplies(commentId)
      } catch (error) {
        console.error('Êèê‰∫§ÂõûÂ§çÂ§±Ë¥•:', error)
        alert('Êèê‰∫§ÂõûÂ§çÂ§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
      }
    }
    
    // ‰øùÂ≠òcommentSectionRefÁî®‰∫éÁõ¥Êé•ËÆøÈóÆÂ≠êÁªÑ‰ª∂
    const commentSectionRef = ref(null)
    
    // Âä†ËΩΩÂõûÂ§çÂàóË°®
    const handleLoadReplies = async (commentId) => {
      try {
        const replies = await getCommentReplies(commentId)
        
        // Â∞ÜÂõûÂ§çÂàóË°®‰øùÂ≠òÂà∞commentsÊï∞ÁªÑ‰∏≠ÂØπÂ∫îËØÑËÆ∫ÁöÑrepliesÂ±ûÊÄß
        const comment = comments.value.find(c => c.id === commentId)
        if (comment) {
          comment.replies = replies || []
        }
        
        // Á≠âÂæÖ‰∏ã‰∏Ä‰∏™tickÔºåÁ°Æ‰øùrefÂ∑≤ÁªèÂàùÂßãÂåñ
        await new Promise(resolve => setTimeout(resolve, 100))
        
        // Êõ¥Êñ∞CommentSectionÁªÑ‰ª∂ÁöÑreplyLists
        if (commentSectionRef.value && commentSectionRef.value.updateReplyList) {
          commentSectionRef.value.updateReplyList(commentId, replies || [])
        }
      } catch (error) {
        console.error(`Âä†ËΩΩËØÑËÆ∫ ${commentId} ÁöÑÂõûÂ§çÂàóË°®Â§±Ë¥•:`, error)
      }
    }
    
    // ËøîÂõûÊåâÈíÆ
    const goBack = () => {
      router.push('/')
    }
    
    // Èü≥‰πêÊéßÂà∂
    const toggleMusic = async () => {
      if (!audioPlayer.value) return
      // Ëã•Êú™ÈÄâÊã©Êàñ‰∏çÊîØÊåÅÁöÑÈü≥‰πêÊ∫êÔºåÁªôÂá∫ÊèêÁ§∫ÔºåÈÅøÂÖç NotSupportedError
      const src = audioPlayer.value.getAttribute('src') || ''
      if (!src) {
        alert('Êú™ÈÄâÊã©ËÉåÊôØÈü≥‰πêÔºåÊó†Ê≥ïÊí≠Êîæ')
        return
      }
      
      if (isPlaying.value) {
        audioPlayer.value.pause()
      } else {
        try {
          audioPlayer.value.volume = 0.3 // Èôç‰ΩéÈü≥Èáè
          await audioPlayer.value.play()
        } catch (error) {
          console.error('Êí≠ÊîæÈü≥‰πêÂ§±Ë¥•:', error)
          alert('Êí≠ÊîæÂ§±Ë¥•ÔºöËØ•ÊµèËßàÂô®ÊàñÂΩìÂâçÊ†ºÂºè‰∏çÂèóÊîØÊåÅ')
        }
      }
      isPlaying.value = !isPlaying.value
    }
    
    // ÊªöÂä®Âà∞ËØÑËÆ∫Âå∫
    const scrollToComments = () => {
      // ‰ºòÂÖà‰ΩøÁî®ÁªÑ‰ª∂Ê†πÂÖÉÁ¥†ÊªöÂä®
      if (commentSectionRef.value && commentSectionRef.value.$el) {
        commentSectionRef.value.$el.scrollIntoView({ behavior: 'smooth', block: 'start' })
        return
      }
      // ÂõûÈÄÄÔºöÊ†πÊçÆÈ°µÈù¢‰∏äÁ¨¨‰∏Ä‰∏™ËØÑËÆ∫ÂàóË°®ÂÆπÂô®ÊªöÂä®
      const el = document.querySelector('.comment-list')
      if (el) el.scrollIntoView({ behavior: 'smooth', block: 'start' })
    }
    
    // ÂΩìÈü≥‰πêÊ∫êÂèòÂåñÊó∂ÔºåÈáçÊñ∞Âä†ËΩΩÂπ∂Âú®ÈúÄË¶ÅÊó∂ÁªßÁª≠Êí≠Êîæ
    watch(musicSrc, async () => {
      if (!audioPlayer.value) return
      try { audioPlayer.value.load() } catch (e) { /* ignore */ }
      if (isPlaying.value) {
        try {
          audioPlayer.value.volume = 0.3
          await audioPlayer.value.play()
        } catch (e) { /* ignore */ }
      }
    })

    let statsTimer = null
    onMounted(async () => {
      await fetchTripDetail()
      await fetchComments()
      // ÂÖ¨ÂºÄË°åÁ®ãËÆ∞ÂΩï‰∏ÄÊ¨°ÊµèËßà
      try {
        const slug = route.params.slug
        if (trip.value && trip.value.overview && trip.value.visibility === 'public') {
          await viewTripPlan(slug)
        }
      } catch (e) {
        // ignore view errors
      }
      // ÁªüËÆ°ËΩÆËØ¢Ôºà‰∏çÂ¢ûÂä†ÊµèËßàÈáèÔºâ
      statsTimer = setInterval(async () => {
        try {
          const slug = route.params.slug
          let stats
          if (trip.value && trip.value.overview) {
            stats = await getTripPlanStats(slug)
          } else {
            stats = await getTripStats(slug)
          }
          if (trip.value) {
            if (!trip.value.stats) trip.value.stats = { views: 0, likes: 0 }
            trip.value.stats.views = stats.views
            trip.value.stats.likes = stats.likes
          }
        } catch (e) {
          // ignore when stats not available
        }
      }, 15000)
    })

    onUnmounted(() => {
      if (statsTimer) clearInterval(statsTimer)
    })
    
    return {
      trip,
      comments,
      loading,
      tripConfig,
      isAdmin,
      isPlaying,
      audioPlayer,
      musicSrc,
      commentSectionRef,
      handleLike,
      handleSubmitComment,
      handleDeleteComment,
      handleAddImage,
      handleUpdateComment,
      handleSubmitReply,
      handleLoadReplies,
      goBack,
      toggleMusic,
      scrollToComments,
      getAvatarUrl,
      isAuthor
    }
  }
}
</script>

